import { collection, addDoc, getDocs, query, where, Timestamp } from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';

export async function inviteUserAsStaff({
  email,
  companyId,
  stationId,
  role,
  message
}: {
  email: string;
  companyId: string;
  stationId?: string;
  role: 'support' | 'technician' | 'station_manager' | 'company_admin';
  message?: string;
}) {
  // 1. Tìm userId qua email
  const q = query(collection(db, 'users'), where('email', '==', email));
  const snap = await getDocs(q);

  if (snap.empty) throw new Error('No user found with this email');

  const userId = snap.docs[0].id;

  // 2. Gửi invitation vào collection 'messages'
  const invitation = {
    userId,
    type: 'invitation',
    role,
    companyId,
    stationId: stationId || null,
    status: 'pending',
    content: message || `You are invited to join our company as ${role}.`,
    createdAt: Timestamp.now(),
  };

  await addDoc(collection(db, 'messages'), invitation);
}
