// components/staff/StaffForm.tsx
'use client';

import { useEffect, useState } from 'react';
import { Input } from '@/src/components/ui/input';
import { Button } from '@/src/components/ui/button';
import { SimpleSelect } from '@/src/components/ui/select';
import { Staff } from '@/src/lib/staff/staffTypes';
import { createStaff, updateStaff } from '@/src/lib/services/staff/staffService';
import { inviteUserAsStaff } from '@/src/lib/staff/inviteUserAsStaff';
import NotificationDialog from '@/src/components/common/NotificationDialog';

interface Props {
  editingStaff: Staff | null;
  companyId: string;
  onSave: () => void;
  onCancel: () => void;
}

const ROLE_OPTIONS = [
  { label: 'Company Admin', value: 'company_admin' },
  { label: 'Station Manager', value: 'station_manager' },
  { label: 'Technician', value: 'technician' },
  { label: 'Support', value: 'support' },
];

export default function StaffForm({ editingStaff, companyId, onSave, onCancel }: Props) {
  const [form, setForm] = useState<Omit<Staff, 'id' | 'createdAt' | 'updatedAt'>>({
    userId: '',
    companyId,
    stationId: '',
    role: 'support',
    name: '',
    email: '',
    phone: '',
  });
  const [dialog, setDialog] = useState<{ open: boolean; type: any; title: string; description?: string }>({
    open: false,
    type: 'info',
    title: '',
    description: '',
  });

  useEffect(() => {
    if (editingStaff) {
      const { id, createdAt, updatedAt, ...rest } = editingStaff;
      setForm(rest);
    }
  }, [editingStaff]);

  const handleChange = (key: keyof typeof form, value: string) => {
    setForm(prev => ({ ...prev, [key]: value }));
  };

  const handleSubmit = async () => {
    if (!form.email || !form.role) {
      setDialog({ open: true, type: 'error', title: 'Missing Info', description: 'Email and Role are required.' });
      return;
    }

    try {
      if (editingStaff) {
        await updateStaff(editingStaff.id, form);
        setDialog({ open: true, type: 'success', title: 'Staff Updated Successfully' });
      } else {
        await inviteUserAsStaff({
          email: form.email,
          companyId,
          stationId: form.stationId || undefined,
          role: form.role,
          message: `${form.name} is invited to join your company as ${form.role}`,
        });
        setDialog({ open: true, type: 'success', title: 'Invitation Sent Successfully' });
      }
      onSave();
    } catch (error: any) {
      setDialog({ open: true, type: 'error', title: 'Error', description: error.message || 'Failed to process request.' });
    }
  };

  return (
    <>
      <div className="space-y-4">
        <Input placeholder="User ID" value={form.userId} onChange={e => handleChange('userId', e.target.value)} disabled={!!editingStaff} />
        <Input placeholder="Full Name" value={form.name} onChange={e => handleChange('name', e.target.value)} />
        <Input placeholder="Email" value={form.email} onChange={e => handleChange('email', e.target.value)} />
        <Input placeholder="Phone" value={form.phone} onChange={e => handleChange('phone', e.target.value)} />
        <Input placeholder="Station ID (optional)" value={form.stationId ?? ''} onChange={e => handleChange('stationId', e.target.value)} />
        <SimpleSelect
          options={ROLE_OPTIONS}
          value={form.role}
          onChange={(val) => handleChange('role', val)}
          placeholder="Select role"
        />
        <div className="flex gap-2">
          <Button onClick={handleSubmit}>Save</Button>
          <Button variant="ghost" onClick={onCancel}>Cancel</Button>
        </div>
      </div>
      <NotificationDialog
        open={dialog.open}
        type={dialog.type}
        title={dialog.title}
        description={dialog.description}
        onClose={() => setDialog({ ...dialog, open: false })}
      />
    </>
  );
}